<template>
  <div>
    <div style="position: absolute; top: 57px; left: 0px; width: 100%; height: 140px;">
      <div style="position: absolute; left: 300px; width: 700px">
          <div style="position: relative;">
            <span style="position:absolute; top: 10px; color: #d81e06; left: 0px;"></span>
            <img src="../assets/shaixuan.png" style="position: absolute;width:20px; top:12px; left: 35px;">
            <div>
              <span style="position: absolute;font-size: 14px; left: 100px; top: 12px;">:</span>
              <el-button type="text" style="position: absolute; left: 180px; top: 1px;"></el-button>
              <span style="position: absolute; left: 230px; top: 11px;">|</span>
              <el-button type="text" style="position: absolute; left: 250px; top: 1px;"></el-button>
              <span style="position: absolute; left: 320px; top: 11px;">|</span>
              <el-button type="text" style="position: absolute; left: 340px; top: 1px;"></el-button>
              <span style="position: absolute; left: 410px; top: 11px;">|</span>
              <el-button type="text" style="position: absolute; left: 430px; top: 1px;"></el-button>
              <span style="position: absolute; left: 500px; top: 11px;">|</span>
              <el-button type="text" style="position: absolute; left: 520px; top: 1px;"></el-button>
              <span style="position: absolute; left: 590px; top: 11px;">|</span>
              <el-button type="text" style="position: absolute; left: 610px; top: 1px;"></el-button>
            </div>
            <div>
              <span style="position: absolute;font-size: 14px; left: 103px; top: 45px;"> </span>
              <el-input-number v-model="count" @change="handleChange" :min="5" :max="20" size="mini" style="position: absolute; left: 170px; top: 40px; "></el-input-number>
              <el-button type="text" style="position: absolute; left: 310px; top: 33px;"></el-button>
            </div>
            <div>
              <span style="position: absolute;font-size: 14px; left:103px; top: 78px;"> </span>
              <el-date-picker @change="handleTimeChange" style="position: absolute; left: 170px; top: 76px;" size="mini" v-model="timepart" type="daterange" range-separator="-" start-placeholder="" end-placeholder=""></el-date-picker>
            </div>
            <div>
              <span style="position:absolute; top: 110px; color: #d81e06; left: 0px;"></span>
              <img src="../assets/sort.png" style="position: absolute;width:20px; top:112px; left: 35px;">
              <div>
                <el-button type="text" style="position: absolute; left: 101px; top: 101px;"></el-button>
                <span style="position: absolute; left: 150px; top: 110px;">|</span>
                <el-button type="text" style="position: absolute; left: 170px; top: 101px;"> <i v-bind:class="countIcon"></i></el-button>
                <span style="position: absolute; left: 220px; top: 110px;">|</span>
                <el-button type="text" style="position: absolute; left: 240px; top: 101px;"> <i v-bind:class="timeIcon"></i></el-button>
                <span style="position: absolute; left: 290px; top: 110px;">|</span>
                <el-button type="text" style="position: absolute; left: 310px; top: 101px;"> <i v-bind:class="hotIcon"></i></el-button>
              </div>
            </div>
          </div>
      </div>
    </div>

    <div style="position: absolute; top: 210px; left: 4%;width: 92%;height:370px;">
      <el-carousel height="370px">
        <el-carousel-item>
          <img src="../assets/carouse1.png" style="width: 100%">
        </el-carousel-item>
        <el-carousel-item>
          <img src="../assets/casourse2.png" style="width: 100%">
        </el-carousel-item>
      </el-carousel>
    </div>

    <div style="position: absolute; top:610px; width:100%;height: 70px;">
      <span style="font-size: 32px; color: black">  /  /  /  </span>
    </div>

    <div style="position: absolute; top:670px; width:100%;">
      <div style="float: left;width:92%; margin-left: 4%;">
        <div v-for="item in projects" :key="item">
          <el-card class="el_card" :body-style="{padding:'0px'}">
            <div style="position: relative;">
              <img style="width: 96%" v-bind:src="item.cover" class="image" v-bind:id="item.missionname">
              <el-tag v-show="item.isContinue" style="position: absolute; left: 20px; top: 10px; color:white; " color="#4CAF50">...</el-tag>
              <el-tag v-show="item.isEnd" style="position: absolute; left: 20px; top: 10px; color:white; " color="#E97506"></el-tag>
              <el-tag v-show="item.isEnd2" style="position: absolute; left: 20px; top: 10px; color:white;width: 60px " color="#E97506"></el-tag>
              <div style="height: 130px;position:absolute; top: 150px; width: 96%; left: 2%; background-color: white">
                <span style="font-size: 20px; color: #4CAF50; width:100%">{{item.missionname}}</span>
                <span class="time" style="position: absolute; left: 5px;top: 30px;font-size: 14px;">{{item.dateStart}} - {{item.dateEnd}}</span>
                <span style="position: absolute; left: 5px;top: 52px;font-size: 14px;">{{item.type}}</span>
                <span style="position: absolute; left: 140px;top: 52px;font-size: 14px;">{{item.counts}}</span>
                <span style="position: absolute; left: 5px;top: 74px;font-size: 14px;">{{item.worker_now_number}} / {{item.worker_total_number}}</span>
                <el-progress style="position: absolute; top: 98px; left:5px;width: 230px; color: black;font-size: 12px;" :percentage="item.percent" :stroke-width="16" :text-inside="true"></el-progress>
              </div>
              <div class="imgOnClick" >
                <img @click="handleProjectDetails(item.id)" src="../assets/img_1.png" style="width: 100%; position: absolute; left: 0px;top: 0px;">
                <p style="color:white;position: absolute; top: 0px; left: 5%; width: 90%;text-align: justify; font-size: 14px">{{item.details}}</p>
                <el-button class="btn" style="position: absolute; left: 50px; bottom: 0px; color: white" type="text" @click="handleProjectDetails(item.id)"></el-button>
                <el-button class="btn2" v-bind:id="item.id" @click="joinProject(item.id)" style="display:block;position: absolute; right: 50px; bottom: 0px; color: white;" type="text" v-bind:disabled="item.isBan"></el-button>
              </div>
            </div>
          </el-card>
        </div>
      </div>
      <div style="float: left; width: 100%;">
        <el-pagination @current-change="handleCurrentChange" background :current-page=1 :page-size="12"
                       layout="total, prev, pager, next, jumper" v-bind:total="project_total">
        </el-pagination>
      </div>
    </div>
    <div>
      <ul>
        <li><el-button class="myProject" type="text" @click="goMyProject"><br></el-button></li>
        <li>
          <a href=""></a>
        </li>
        <li>
          <a href="javascript:window.scrollTo(0,0);"></a>
        </li>
      </ul>
    </div>

  </div>

</template>

<script>
export default {
  name: 'CountsMarket',
  data () {
    return {
      project_total: 0,
      count: 1,
      timepart: '',
      countIcon: 'el-icon-sort-down',
      timeIcon: 'el-icon-sort-down',
      hotIcon: 'el-icon-sort-down',
      project_info: {
        cover: 'http://localhost:8080/static/img/home_back.00f1c22.png',
        missionname: 'mission',
        counts: 5,
        details: 'ImageNetXML',
        dateStart: '2018/04/12',
        dateEnd: '2018/09/27',
        worker_total_number: 30,
        worker_now_number: 16,
        percent: (16 / 30 * 100).toFixed(2),
        type: '',
        id: '001002',
        isContinue:true,
        isEnd: false,
        isBan:true,
      },
      projects: []
    }
  },
  created () {
    var xmlhttp = new XMLHttpRequest()
    var _this = this
    xmlhttp.onreadystatechange = function () {
      if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
        var arrays = JSON.parse(xmlhttp.responseText)
        _this.project_total = arrays.length
        for (var i = 0; i < arrays.length; i++) {
          var e = arrays[i]
          var info = {
            cover: '',
            missionname: e.missionName,
            counts: e.points,
            details: e.description,
            dateStart: e.begin,
            dateEnd: e.end,
            worker_total_number: e.maxNumber,
            worker_now_number: e.currentNumber,
            percent: (e.currentNumber / e.maxNumber * 100).toFixed(2),
            type: e.type,
            id: e.id,
            isContinue:true,
            isEnd:false,
            isEnd2: false,
          }

          _this.projects.push(info)
        }
        for(var i=0;i<_this.projects.length;i++){
          var time = _this.projects[i].dateEnd.split('-');
          var end = new Date(time[0], parseInt(time[1])-1, parseInt(time[2])+1)
          var now = new Date()
          if(now <= end){
            _this.projects[i].isEnd2=false;
            if(_this.projects[i].worker_now_number<_this.projects[i].worker_total_number) {
              _this.projects[i].isContinue = true;
              _this.projects[i].isEnd = false;
            }else{
              _this.projects[i].isContinue = false;
              _this.projects[i].isEnd = true;
            }
          }else{
            _this.projects[i].isEnd2 = true;
            _this.projects[i].isEnd = false;
            _this.projects[i].isContinue = false;
          }
          _this.projects[i].isBan = !_this.projects[i].isContinue;
          if(localStorage.getItem('identify')=='requester'){
            _this.projects[i].isBan = true;
          }
          _this.getCoverImg(_this.projects[i].id, i);
        }
      }
    }
    let formData = new FormData()
    formData.append('i', '1')
    xmlhttp.open('POST', 'http://localhost:8080/counts/mission/getmission', true)
    xmlhttp.send(formData)
  },
  methods: {

    getCoverImg (missionid, i) {
      var cover = ''
      var _this = this
      var xmlhttp = new XMLHttpRequest()
      xmlhttp.onreadystatechange = function () {
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
          cover = xmlhttp.responseText
          _this.projects[i].cover = cover
        }
      }
      let formData = new FormData()
      var str = '' + missionid
      formData.append('missionid', str)
      var path = localStorage.getItem('server')+'/counts/mission/get/firstimage'
      xmlhttp.open('POST',path, true)
      xmlhttp.send(formData)
    },

    handleCurrentChange (val) {
      console.log(val)
      var xmlhttp = new XMLHttpRequest()
      var _this = this
      this.projects = []
      xmlhttp.onreadystatechange = function () {
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
          var arrays = JSON.parse(xmlhttp.responseText)
          _this.project_total = arrays.length
          for (var i = 0; i < arrays.length; i++) {
            var e = arrays[i]
            var info = {
              cover: '',
              missionname: e.missionName,
              counts: e.points,
              details: e.description,
              dateStart: e.begin,
              dateEnd: e.end,
              worker_total_number: e.maxNumber,
              worker_now_number: e.currentNumber,
              percent: (e.currentNumber / e.maxNumber * 100).toFixed(2),
              type: e.type,
              id: e.id,
              isContinue:true,
              isEnd:false,
              isEnd2: false,
            }

            _this.projects.push(info)
          }
          for(var i=0;i<_this.projects.length;i++){
            var time = _this.projects[i].dateEnd.split('-');
            var end = new Date(time[0], parseInt(time[1])-1, parseInt(time[2])+1)
            var now = new Date()
            if(now <= end){
              _this.projects[i].isEnd2=false;
              if(_this.projects[i].worker_now_number<_this.projects[i].worker_total_number) {
                _this.projects[i].isContinue = true;
                _this.projects[i].isEnd = false;
              }else{
                _this.projects[i].isContinue = false;
                _this.projects[i].isEnd = true;
              }
            }else{
              _this.projects[i].isEnd2 = true;
              _this.projects[i].isEnd = false;
              _this.projects[i].isContinue = false;
            }
            _this.projects[i].isBan = !_this.projects[i].isContinue;
            _this.getCoverImg(_this.projects[i].id, i);
          }
        }
      }
      let formData = new FormData()
      formData.append('i', ''+val)
      xmlhttp.open('POST', 'http://localhost:8080/counts/mission/getmission', true)
      xmlhttp.send(formData)
    },
    openSucc: function (text) {
      this.$notify({
        title: '',
        message: text,
        type: 'success',
        duration: 2000,
        position: 'top-left'
      })
    },
    openInfo (text) {
      this.$notify({
        title: '',
        message: text,
        type: 'info',
        duration: 2000,
        position: 'top-left'
      })
    },
    goMyProject () {

      var path = '/' + localStorage.getItem('username')
      if (localStorage.getItem('identify') == 'worker') {
        this.$router.push({path: path + '/myProject'})
      } else if (localStorage.getItem('identify') == 'requester') {
        this.$router.push({path: path + '/requester'})
      } else if (localStorage.getItem('identify') == 'logout' || localStorage.getItem('username') == 'visitor') {
        this.openInfo("You haven't logged in")
      }
      window.location.reload()
    },

    joinProject (id) {
      if (localStorage.getItem('identify') == 'requester') {
        this.openInfo("Sorry, you can't join this project as a requester.")
        return;
      } else if (localStorage.getItem('identify') == 'logout') {
        this.openInfo("You haven't logged in. Please login first.")
        return;
      }
      var xmlhttp = new XMLHttpRequest()
      var _this = this
      xmlhttp.onreadystatechange = function () {
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
          if (JSON.parse(xmlhttp.responseText).result == true) {
            _this.openSucc('')
          }else{
            _this.openInfo('')
          }
        }
      }
      let formData = new FormData()
      var str = '' + id
      formData.append('missionid', str)
      var name = localStorage.getItem('userid')
      formData.append('userid',name)
      console.log(name)
      var path = localStorage.getItem('server')+'/counts/user/acceptmission'
      xmlhttp.open('POST', path, true)
      xmlhttp.send(formData)
    },

    handleProjectDetails (id) {
      localStorage.setItem('missionID', id)
      var path = '#/' + localStorage.getItem('username') + '/countsMarket/' + id + '/projectDetails'
      window.open(path)
    },

    handleChange (value) {

    },
    handleTimeChange () {

    }

  }
}
</script>

<style scoped>
  .imgOnClick {
    width: 96%;
    height:270px;
    display: none;
    position: absolute;
    left: 2%;
    top: 0px;
  }

  .el_card:hover .imgOnClick{
    display:block;
  }

  .el_card{
    border-radius: 0;
    margin: 15px;
    padding: 5px;
    width: 250px;
    height: 270px;
    float: left;
    position: relative;
  }

  ul{
    position: fixed;
    right: 0px;
    top: 230px;
    font-size: 14px;
    list-style-type: none;
    margin: 0;
    padding: 0px;
    width: 53px;
    background-color: #f1f1f1;
  }
  li{
    height: 60px;
  }
  li:hover{
  }
  li a {
    display: block;
    color: #000;
    padding: 8px;
    text-decoration: none;
  }
  li a:hover:not(.active){
    background-color:  #4CAF50;
    color: white;
  }
  li .myProject{
    line-height: 1.5;
    padding:8px;
    border-radius: 0;
    width: 53px;
    font-size: 14px;
    color: #000;
  }
  li button:hover:not(.active){
    background-color:  #4CAF50;
    color: white;
  }
  .btn:hover{
    text-decoration: underline;
  }
  .btn2:hover{
    text-decoration: underline;
  }
</style>



