      drawMissionNum() {
        var myChart = echarts.init(document.getElementById("missionNum"));
        var totalData = [320, 332, 301, 334, 390, 400];
        var finishedData = [220, 182, 191, 234, 290, 330];
        var doingData = [150, 232, 201, 154, 190, 210];
        var xmlhttp = new XMLHttpRequest();
        xmlhttp.onreadystatechange = function () {
          if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
            console.log("1234")
            finishedData = JSON.parse(xmlhttp.responseText).list1;
            doingData = JSON.parse(xmlhttp.responseText).list2;
            totalData = JSON.parse(xmlhttp.responseText).list3;
          }
        }
        xmlhttp.open('GET', 'http://localhost:8080/counts/analysis/getmission/monthcart', false);
        xmlhttp.setRequestHeader("Content-type", "application/json; charset=utf-8");
        xmlhttp.send(null);


        var config = {
          rotate: 90,
          align: 'left',
          verticalAlign: 'middle',
          position: 'insideBottom',
          distance: 15,
          onChange: function () {
            var labelOption = {
              normal: {
                rotate: app.config.rotate,
                align: app.config.align,
                verticalAlign: app.config.verticalAlign,
                position: app.config.position,
                distance: app.config.distance
              }
            }
            myChart.setOption({
              series: [{
                label: labelOption
              }, {
                label: labelOption
              }, {
                label: labelOption
              }, {
                label: labelOption
              }]
            })
          }
        }

        var labelOption = {
          normal: {
            show: true,
            position: config.position,
            distance: config.distance,
            align: config.align,
            verticalAlign: config.verticalAlign,
            rotate: config.rotate,
            formatter: '{c}  {name|{a}}',
            fontSize: 16,
            rich: {
              name: {
                textBorderColor: '#fff'
              }
            }
          }
        }

        var option = {
          color: ['#003366', '#006699', '#4cabce', '#e5323e'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            data: ['', '', '']
          },
          toolbox: {
            show: true,
            orient: 'vertical',
            left: 'right',
            top: 'center',
            feature: {
              mark: {show: true},
              dataView: {show: true, readOnly: false},
              magicType: {show: true, type: ['line', 'bar', 'stack', 'tiled']},
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          calculable: true,
          xAxis: [
            {
              type: 'category',
              axisTick: {show: false},
              data: ['2018-1', '2018-2', '2018-3', '2018-4', '2018-5', '2018-6']
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '',
              type: 'bar',
              barGap: 0,
              label: labelOption,
              data: totalData
            },
            {
              name: '',
              type: 'bar',
              label: labelOption,
              data: finishedData
            },
            {
              name: '',
              type: 'bar',
              label: labelOption,
              data: doingData
            }

          ]
        }

        myChart.setOption(option)
      },

    },
    mounted: function () {
      this.drawWorkerNum()
      this.drawRequstorNum()
      this.drawUserNum()
      this.drawMissionComplete()
      this.drawUserDistribution()
      this.drawMissionNum()
    }
  }
