          <el-form-item label="" prop="newMail" required>
            <el-input  v-model="connectform.newMail" auto-complete="off" size="small" style="width: 300px"></el-input>
          </el-form-item>
          <el-form-item label="" prop="newPhone" :label-width="formLabelWidth" required>
